<script setup>
import HeadVue from '../components/HeadVue.vue';

</script>
<template>
    <div class="container">
     <HeadVue
     Head = 'Список заведений'
     Close = 'true'
     Question = 'false'
     />
     <div class="nav">
        <button @click="ModalStart">test</button>
     </div>
     <div class="content">
     </div>
    </div>
    <div 
    v-if="modal == true"
    class="modal_back"
    :class="{ 'modal_back': true, 'show': modal }">
        <div class="modal_container" :class="{active: (active == true)}">
            <button @click="ModalEnd">закрыть</button>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            modal: false,
            active: false
        }
    },
    methods: {
        ModalStart() {
            this.modal = true
            setTimeout (async() =>{
                this.active = true
            },100 )
        },
        ModalEnd() {
            this.active = false
            setTimeout (async() =>{
                this.modal = false
            },200 )
        }
    }
}
</script>

